$font-path: "./assets/fonts/";

/* roboto-300 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("#{$font-path}roboto-v18-latin-300.eot"); /* IE9 Compat Modes */
  src: local("Roboto Light"), local("Roboto-Light"),
    url("#{$font-path}roboto-v18-latin-300.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("#{$font-path}roboto-v18-latin-300.woff2") format("woff2"),
    /* Super Modern Browsers */ url("#{$font-path}roboto-v18-latin-300.woff")
      format("woff"),
    /* Modern Browsers */ url("#{$font-path}roboto-v18-latin-300.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("#{$font-path}roboto-v18-latin-300.svg#Roboto") format("svg"); /* Legacy iOS */
}
